@import './_reset.scss';

$mainColor : tomato;
$blueColor : dodgerblue;

@mixin alert {
  background: #eeeeee;
  padding: 20px;
  border-radius: 5px;
  max-width: 500px;
  width: 100%;
  margin: auto;
}

div.container{
  .red{
    color: $mainColor;
  }

  h4 {
    color :$blueColor;
  }

  .my-alert{
    @include alert;
  }

  .my-alert2{
    @extend .my-alert;
    background: #ffe591;

    p{margin: 0;}
  }


}

